
svg {
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
}
example_f {
   border-radius: 4px;
   background: linear-gradient(to right, #67b26b, #4ca2cb) !important;
   border: none;
   color: #FFFFFF;
   text-align: center;
   text-transform: uppercase;
   font-size: 22px;
   padding: 20px;
   width: 200px;
   transition: all 0.4s;
   cursor: pointer;
   margin: 5px;
}
.example_f span {
   cursor: pointer;
   display: inline-block;
   position: relative;
   transition: 0.4s;
}
.example_f span:after {
   content: '\00bb';
   position: absolute;
   opacity: 0;
   top: 0;
   right: -20px;
   transition: 0.5s;
}
.example_f:hover span {
   padding-right: 25px;
}
.example_f:hover span:after {
   opacity: 1;
   right: 0;
}
a { color: #ff6600; transition: .5s; -moz-transition: .5s; -webkit-transition: .5s; -o-transition: .5s; font-family: 'Muli', sans-serif;
}
a:hover { text-decoration: underline
}
h1 {
  font-size: 72px;
  background: -webkit-linear-gradient(#202, #b9b);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
h4 {
  font-size: 22px;
  background: -webkit-linear-gradient(#300, #fc0);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
h1 a { font-family: 'Open Sans Condensed', sans-serif; font-size: 80px; color: #333;
}
button { font-family: 'Open Sans Condensed', sans-serif; font-size: 25px; color: #333;
}
h1 a:hover { color: #ff6600; text-decoration: none;
}
p { color: #333; font-family: 'Muli', sans-serif; margin-bottom: 15px;
}
a.more-link { color: white; font-weight: bold; font-size: 14px; font-family: Arial, Helvetica, sans-serif; padding: 3px 10px; background-color: #ff6600; border-radius: 5px; float: right;
}
a.more-link:hover { text-decoration: none; background-color: #666; border-radius: 0px;
}
.container {
    width: 100%;
    margin: 20px auto 0px auto;
}
html, body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  margin-top: 16px;
  margin-bottom: 16px;
}
div#app {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
.search-wrapper {
    position: relative;
label {
      position: absolute;
      font-size: 12px;
      color: rgba(0,0,0,.50);
      top: 8px;
      left: 12px;
      z-index: -1;
      transition: .15s all ease-in-out;
}
input {
      padding: 4px 12px;
      color: rgba(0,0,0,.70);
      border: 1px solid rgba(0,0,0,.12);
      transition: .15s all ease-in-out;
      background: white;
&:focus {
        outline: none;
        transform: scale(1.05);
& + label  {
          font-size: 10px;
          transform: translateY(-24px) translateX(-12px);
}
}
&::-webkit-input-placeholder {
          font-size: 12px;
          color: rgba(0,0,0,.50);
          font-weight: 100;
}
}
}
.wrapper {
    display: -ms-flexbox;
    display: flex;
    max-width: 444px;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    padding-top: 12px;
}
.card {
    box-shadow: rgba(0, 0, 0, 0.117647) 0px 1px 6px, rgba(0, 0, 0, 0.117647) 0px 1px 4px;
    max-width: 124px;
    margin: 12px;
    transition: .15s all ease-in-out;
&:hover {
      transform: scale(1.1);
}
a {
      text-decoration: none;
      padding: 12px;
      color: #03A9F4;
      font-size: 24px;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-direction: column;
          flex-direction: column;
      -ms-flex-align: center;
          align-items: center;
img {
        height: 100px;
}
small {
        font-size: 10px;
        padding: 4px;
}
}
}
.hotpink {
    background: hotpink;
}
.green {
    background: green;
}
.box {
    width: 100px;
    height: 100px;
    border: 1px solid rgba(0,0,0,.12);
}
}




